export default "<template>\r\n  <figure>\r\n    <img src=\"https://raw.githubusercontent.com/Turkyden/imagehover-effects/master/placeholder.png\" />\r\n    <figcaption>\r\n      Hello World ! üê∂\r\n    </figcaption>\r\n  </figure>\r\n</template>\r\n\r\n<style scoped>\r\nfigure {\r\n  position: relative;\r\n  display: inline-block;\r\n  margin: 0;\r\n  max-width: 100%;\r\n  background-color: #2266a5;\r\n  color: #fff;\r\n  overflow: hidden;\r\n  -webkit-backface-visibility: hidden;\r\n  backface-visibility: hidden;\r\n  -moz-osx-font-smoothing: grayscale;\r\n  -webkit-transform: translateZ(0);\r\n  transform: translateZ(0);\r\n}\r\n\r\nfigure > img {\r\n  vertical-align: top;\r\n  max-width: 100%;\r\n}\r\n\r\nfigure figcaption {\r\n  background-color: #135796;\r\n  padding: 30px;\r\n  position: absolute;\r\n  top: 0;\r\n  bottom: 0;\r\n  left: 0;\r\n  right: 0;\r\n}\r\n\r\nfigure,\r\nfigure:before,\r\nfigure:after,\r\nfigure *,\r\nfigure *:before,\r\nfigure *:after {\r\n  box-sizing: border-box;\r\n  transition: all 0.35s ease;\r\n}\r\n\r\n/** shutter-in- **/\r\nfigure:before, figure:after {\r\n  background: #135796;\r\n  position: absolute;\r\n  content: '';\r\n}\r\n\r\nfigure:after {\r\n  top: 0;\r\n  left: 0;\r\n}\r\n\r\nfigure:before{\r\n  right: 0;\r\n  bottom: 0;\r\n}\r\n\r\nfigure figcaption {\r\n  opacity: 0;\r\n  z-index: 1;\r\n}\r\n\r\nfigure:hover figcaption{\r\n  opacity: 1;\r\n  transition-delay: 0.21s;\r\n}\r\n\r\n/** shutter-in-out-vert **/\r\nfigure:after, figure:before {\r\n  height: 0;\r\n  width: 100%;\r\n  opacity: 0.75;\r\n}\r\n\r\nfigure:hover:after, figure:hover:before {\r\n  height: 100%;\r\n}\r\n</style>\r\n";